@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
body {
  margin: 0;
  padding: 0;
}

header {
  height: 5rem;
  width: 100%;
  background: #E92731;
  color: black;
  border-bottom: 8px solid #018994;
  padding: .5rem;
  position: fixed;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

h1 {
  margin: 0;
  width: 60%;
  font-family: Rubik, sans-serif;
  text-shadow: 1px 2px 10px #018994, 0 0 0.2em #fff;
}

.home-icon {
  align-self: flex-start;
  width: 2rem;
  margin-right: 0.9rem;
}

@media screen and (min-width: 1000px) {
  .home-icon:hover {
    border: 3px solid #fff;
    border-radius: 99%;
  }
}


.error-message {
  position: relative;
  top: 8rem;
}

.movies-container {
  width: 90%;
  margin-top: 9rem;
  display: grid;
  grid-template-columns: repeat( auto-fit, minmax(150px, 1fr) );
  grid-gap: 16px;
}



.nav {
  color: black;
  text-decoration: none;
}

.nav:visited {
  color: #E92731;
}

.card {
  height: 19rem;
}

.card-poster {
  width: 150px;
  box-shadow: 5px 3px 15px #018994;
  border-radius: 8px;
  background: #018994;
}

.card-title {
  height: 1.2rem;
  margin-top: .5rem;
  text-align: center;
  font-size: 1.1rem;
  font-family: Rubik, sans-serif;
  font-weight: 500;
}

@media screen and (min-width: 1024px) {
  .card:hover {
    transform: scale(1.1);
    text-decoration: underline;
    color: #018994;
    cursor: pointer;
  }

  .card:hover .card-poster {
    box-shadow: 5px 3px 15px #E92731;
  } 
}

.single-film {
  margin: 7rem 1rem 1rem 1rem;
  font-family: Rubik, sans-serif;
  text-align: left;
  text-align: initial;
  max-width: 1200px;
  display: grid;
  grid-template-columns: 2fr 4fr;
  grid-template-areas:
    "details details"
    "poster overview";
  grid-gap: 10px;
}

.film-details {
  grid-area: details;
}

.single-film img {
  grid-area: poster;
  width: 100%;
  max-width: 330px;
}

.overview {
  grid-area: overview;
}

/* Styling for title area */
.film-title-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  margin-bottom: .8rem;
}

.title {
  margin: 0;
  text-decoration: underline;
}

.rating {
  align-self: flex-end;
  margin: 0;
}

/* Styling for runtime, genres, and release date */
.info-container {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  grid-column-gap: 1rem;
  -webkit-column-gap: 1rem;
          column-gap: 1rem;
  width: 100%;
  max-height: 30px;
  font-size: 1.2rem;
  text-align: center;
}

.info-container h4 {
  background: white;
  margin-bottom: .5rem;
  width: 100%;
  height: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.info-container>h4:nth-child(1) {
  text-align: left;
  text-align: initial;
}

.genre-display {
  width: 100%;
  border-right: 3px solid black;
  border-left: 3px solid black;
  padding-left: 5px;
  padding-right: 5px;
}

/* Styling for the summary section */
.summary {
  width: 100%;
}

.summary>p {
  font-weight: 100;
  margin-top: 0;
  margin-bottom: 0;
}

.summary-headline {
  font-size: 2rem;
  margin-top: 0;
  margin-bottom: 0.8rem;
}

.money-container {
  font-size: 1.2rem;
  font-weight: 100;
}

/* Styling for the financial info at the bottom */
.money-container>h4 {
  margin-bottom: 0;
}

.money-container>h5 {
  margin-top: 0;
}

@media screen and (min-width: 400px) {
  .single-film {
    margin-left: 2rem;
    margin-right: 2rem;
  }
}

@media screen and (min-width: 600px) {
  .single-film {
    grid-template-columns: 330px 1fr;
    margin-left: 3rem;
    margin-right: 3rem;
  }

  .info-container {
    max-width: 800px;
  }
}
.App {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: calc(10px + 2vmin);
  text-align: center;
}

.message {
  position: relative;
  top: 6rem;
  font-family: Rubik, sans-serif;
}

